body {
  margin: 0 0 0 0;
}

.title {
    margin-left: 100px;
}

.YearCategoryElement {
  display: flex;
  cursor: pointer;
  border: solid 1px;
  margin: 1px 0 1px 10px;
  border-color: #b1a1da;
  padding: 1px 5px 1px 1px;
}
.YearCategoryElement:hover {
  background-color: #b1a1da;
}

.Year {
  margin: 3px 10px 3px 10px;
}

.Laureates {
  display: flex;
  margin: 50px 0 0 0;
}

.TableInfo {
  position: fixed;
  left: 200px;
}

.ListOfLaureates_header {
  padding-left: 200px;
  margin: 0 0 10px 0;
}

.firstname {

}

.surname {
  margin: 0 0 0 10px;
}

.motivation {
  font-style: italic;
  margin: 0 0 0 10px;
}

.laureat {
  display: flex;
  justify-content: space-between;
  margin: 5px 10px 5px 0;
  border-style: solid;
  border-color: #b1a1da;
  border-width: 1px;
  padding: 5px;
}

.header {
  position: fixed;
  top: 0px;
  background-color: #b1a1da;
  width: 100%;
  border-radius: 0 0 5px 5px;
  height: 6%;

}

.listOfYearCategoryElements {
  margin: 10% 0 0 0;
}

.filters {
  display: flex;
  margin: 1% 0 0 0;
  height:inherit;
}

.element {
  display: flex;
}
.indicator{
  width: 10px;
  cursor: pointer;
  margin: 0 10px 0 0 ;
}
.value {
  margin: 0 0 0 10px;
}
.name {
  color: darkturquoise;
}
.dropedList {
  margin: 0 0 0 10px;
}
.input {
  width: 50%;
}